/* Общие стили для страницы */
body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #121212;
  color: #e0e0e0;
}

.container {
  position: relative;
  height: 100vh;
  overflow: hidden;
}

/* Контейнер для кнопок меню — овальное меню внизу */
.menu-button-container {
  position: fixed;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px; /* Равномерное расстояние между кнопками */
  background-color: rgba(30, 30, 30, 0.6); /* Полу-прозрачный фон */
  padding: 15px 30px;
  border-radius: 35px; /* Полностью овальная форма */
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.8); /* Парящий эффект */
  backdrop-filter: blur(8px); /* Размытие фона для "стеклянного" вида */
  z-index: 1000;
}

/* Стиль для кнопок */
.menu-button {
  background-color: transparent;
  color: #e0e0e0;
  border: none;
  font-size: 0.9rem;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 6px;
  min-width: 60px;
  text-align: center;
  transition: color 0.3s ease;
}

/* Стиль для иконок в кнопках */
.menu-button .menu-icon {
  font-size: 26px;
  color: #cccccc;
  line-height: 1;
}

/* Контейнер для контента */
.content {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.5s ease-in-out;
  background-color: #1a1a1a;
  padding: 0 20px;
  position: absolute;
  top: 0;
  width: 100%;
  color: #cccccc;
}

.content.visible {
  max-height: 200px;
  padding: 20px;
}
